import {Col, Row, theme, Layout, Typography, Button } from 'antd';
import { Link, } from 'react-router-dom';

import Header from '../../components/Header/Header';
import Footer from  '../../components/Footer/Footer';
import Carousel from  '../../components/Carousel/Carousel';

const { Paragraph, Title } = Typography;

export default () => {    
    return (
        <Layout >
            <Header />
            <div
                style={{
                    padding: '0 10%',
                }}
            >
                <Row>
                    <Col 
                    // span={12}
                     xl={12}
                     xs={24}
                     >
                        <Title>About Me</Title>
                        <Paragraph>
                            Hi! My name is Mikita Pemb. Im originally from Vitebsk, Belarus, but now I live in Gdask, Poland. For over ten years, graffiti has been my passion and my way of self-expression. Since 2020, Ive been channeling the energy of the streets onto canvases and creating unique cover designs for music releases. My work is a genuine reflection of my journeya blend of the vibrant chaos of street art with a desire to push boundaries and explore new creative horizons.
                        </Paragraph>
                        <Paragraph>
                            In my technique, I combine the boldness of spray paint with the versatility of acrylics, adding fine details using squeezers and markers. Every piece I create is a celebration of the style I discovered on the streets, transformed into something that resonates with a broader audience.
                        </Paragraph>
                        <Paragraph>
                            Ive had the privilege of participating in various exhibitions and constantly strive to promote my unique artistic vision. My dream is to collaborate with galleries and establish myself in larger, more ambitious art spaces, as I believe my style has the potential to reach far beyond the world of street art.
                        </Paragraph>
                        <Paragraph>
                            On this platform, youll only see my art projects, but Im happy to share my graffiti work with anyone whos interested. In addition to speaking Polish at a conversational level, Im also learning English, which opens new opportunities to showcase my art and connect with an international audience. Art isnt just something I doits who I am.
                        </Paragraph>
                        <Paragraph>
                            Lets create something extraordinary together!
                        </Paragraph>
                        <Button value="default"><Link to="/gallery">Gallery</Link></Button>
                    </Col>
                    <Col 
                        xl={12}
                        xs={24}
                        >
                        <Carousel />
                    </Col>
                </Row>
            </div>
            <Footer />
        </Layout>
    )
}

